import { useEffect, useState } from 'react'
import './Second_content.css'

let numberOfPage = 0  

setInterval(()=>{

    if(numberOfPage >= 3){

        numberOfPage = 0

    }

    numberOfPage += 1

}, 3000)




const Second_content = () => {

    
    let [numBG , setNumBG] = useState(0)


    setInterval(() => {

        setNumBG(numberOfPage)
        
    }, 3000);


    useEffect(()=>{

        console.log("num-page : " + numBG);

    },[numBG])


    return (

        <div className="Second_content">

            <div className="second_title_in_main">
                COFFEE
            </div>
            
            <div className="second_text_in_main">
            
                <div className="second_text">
                    Lorem ipsum dolor sit amet consectetur adipisicing elit. 
                    Aliquid laboriosam, minima maxime dolore doloribus asperiores 
                    sunt cum repellendus vitae saepe pariatur dicta nisi. 
                </div>
            
            </div>
            
            <div className="second_btn_in_main">
                <button>
                    view pictures
                </button>
            </div>

        </div>

    )
}

export default Second_content